<template>
  <div class="hello">
    <button @click="handleMsg">Get Message</button>
    <h1>{{ msg }}</h1>
  </div>
</template>

<script>
import {getmsg} from "@/api/index.js";

export default {
  name: 'HelloWorld',
  data() {
    return {
      msg:''
    }
  },
  methods: {
    handleMsg() {
      getmsg().then(res => {
        this.msg = res.data
      })
    }
  }

}
</script>

